import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Works from '@/components/Works'
import Aboutus from '@/components/Aboutus'
import Advantage from '@/components/Advantage'
import Customer from '@/components/Customer'
import Contact from '@/components/Contact'
import Culture from '@/components/Culture'
import Newsreport from '@/components/NewsReport'
import Joinus from '@/components/JoinUs'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },{
      path: '/works/:type',
      name: 'Works',
      component: Works
    },{
      path: '/aboutus',
      name: 'Aboutus',
      component: Aboutus
    },{
      path: '/advantage',
      name: 'Advantage',
      component: Advantage
    },{
      path: '/customer',
      name: 'Customer',
      component: Customer
    },{
      path: '/contact',
      name: 'Contact',
      component: Contact
    },{
      path: '/culture',
      name: 'Culture',
      component: Culture
    },{
      path: '/news',
      name: 'Newsreport',
      component: Newsreport
    },{
      path: '/join',
      name: 'Joinus',
      component: Joinus
    }
  ]
})



