<template>
    <div class="container">
        <div class="pages">
            <div class="mainwrap">
                <div class="panel-hd">
                    <div class="emphasis-name"><strong>NEWS</strong><em>|</em><span>热点新闻</span></div>
                </div>
                <ul class="news-wrap">
                    <li v-for="(item, index) in currentPageData">
                        <div class="pic-lk"><a class="tag-img"><img :src="item.ul" /></a></div>
                        <div class="new-info">
                            <div class="time">{{item.tm}}</div>
                            <h3 class="tle">{{item.tl}}</h3>
                            <p class="abstract">{{item.ab}}</p>
                            <a class="clk-read" @click="handlePopup(item.id)">&gt; 阅读这篇文章</a>
                        </div>
                    </li>
                </ul>
                <nav class="srp-pager" aria-label="Page navigation">
                    <el-pagination @current-change="handleCurrentChange" :current-page="page" :page-count="total"></el-pagination>
                </nav>
            </div>
            <div class="sidebar">
                <cnode-sidebar></cnode-sidebar>
            </div>
        </div>
        <el-dialog :visible.sync="dialogVisible" width="100%" fullscreen>
            <div class="container">
                <div class="dialog-row row">
                    <div class="ascription">
                    </div>
                    <div class="work-show" v-html="ctc.de"></div>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import cnodeSidebar from '@/components/Sidebar'
import elPagination from '@/components/pagination'
import elDialog from '@/components/Dialog'

function filterTableData(ary, key, val){
    return ary.filter(function(element){
        return element[key] === val;
    });
}

export default {
    name: 'Newsreport',
    components: {
        cnodeSidebar,
        elPagination,
        elDialog
    },
    data() {
        return {
            loading: true,
            total: 0,
            page: 1,
            tableData: [],
            ctc: {
                tl: '',
                ab: '',
                tm: '',
                de: ''
            },
            dialogVisible: false
        };
    },
    computed: {
        currentPageData() {
            var pc = 4;
            return this.tableData.slice((this.page - 1) * pc, this.page * pc);
        }
    },
    methods: {
        handleCurrentChange(val) {
            this.loading = true;
            this.page = val;
            this.handleCurrentData();
        },
        handleCurrentData(){
            setTimeout(()=>{
                this.loading = false;
            }, 300 + Math.round(Math.random()*200));
        },
        handlePopup (pt) {
            var ct = filterTableData(this.currentPageData, 'id', pt)[0];
            this.ctc.tl = ct.tl;
            this.ctc.ab = ct.ab;
            this.ctc.tm = ct.tm;
            this.ctc.de = ct.de;
            this.dialogVisible = true;
        },
        getData() {
            this.$http({
                url: '/static/mongodb-news.json',
                method: 'get',
            }).then((res) => {
                this.tableData = res.data.news;
                this.inc = parseInt(res.data.inc);
                this.total = Math.ceil(this.tableData.length/4);
                this.handleCurrentData();
            }).catch((res) => {
                console.log('Customer.vue: ', res);
            });
        }
    },
    mounted() {
        this.getData();
    }
}
</script>



