<template>
    <div class="container">
        <div class="pages">
            <div class="mainwrap">
                <div class="panel-hd">
                    <div class="emphasis-name"><strong>CUSTOMER</strong><em>|</em><span>客户</span></div>
                </div>
                <div class="row customer-wrap">
                    <div class="col-xs-3" v-for="(item, index) in currentPageData">
                        <div class="tagimg-ct">
                            <img :title="item.na" :src="item.ul" />
                        </div>
                    </div>
                </div>
                <nav class="srp-pager customer-pager" aria-label="Page navigation">
                    <el-pagination @current-change="handleCurrentChange" :current-page="page" :page-count="total"></el-pagination>
                </nav>
            </div>
            <div class="sidebar">
                <cnode-sidebar></cnode-sidebar>
            </div>
        </div>
    </div>
</template>

<script>
import cnodeSidebar from '@/components/Sidebar'
import elPagination from '@/components/pagination'

export default {
    name: 'Customer',
    components: {
        cnodeSidebar,
        elPagination
    },
    data() {
        return {
            loading: true,
            total: 0,
            page: 1,
            tableData: [],
        };
    },
    computed: {
        currentPageData() {
            var pc = 16;
            return this.tableData.slice((this.page - 1) * pc, this.page * pc);
        }
    },
    methods: {
        handleCurrentChange(val) {
            this.page = val;
        },
        handleCurrentData(){
            setTimeout(()=>{
                this.loading = false;
            }, 300 + Math.round(Math.random()*200));
        },
        getData() {
            this.$http({
                url: '/static/mongodb-brand.json',
                method: 'get',
            }).then((res) => {
                this.tableData = res.data.brand;
                this.inc = parseInt(res.data.inc);
                this.total = Math.ceil(this.tableData.length/16);
                this.handleCurrentData();
            }).catch((res) => {
                console.log('Customer.vue: ', res);
            });
        }
    },
    mounted() {
        this.getData();
    }
}
</script>



