<template>
  <div>
  <ul @click="onPagerClick" class="pagination">
    <li :class="{ disabled: currentPage <= 1 }">
        <a href="#" aria-label="Previous"><span aria-hidden="true">首页</span></a>
    </li>
    <li
      :class="{ active: currentPage === 1 }"
      v-if="pageCount > 0"><a href="#">1</a></li>
    <li
      :class="{ active: currentPage === 2 }"
      v-if="pageCount > 1"><a href="#">2</a></li>
    <li v-if="showPrevMore"><span>...</span></li>
    <li
      v-for="pager in pagers"
      :class="{ active: currentPage === pager }"
      class="number"><a href="#">{{ pager }}</a></li>
    <li v-if="showNextMore"><span>...</span></li>
    <li :class="{ disabled: currentPage === pageCount || pageCount === 0 }">
        <a href="#" aria-label="Next"><span aria-hidden="true">尾页</span></a>
    </li>
  </ul>
  <span class="total" v-if="pageCount > 1">当前 {{ currentPage }}/{{ pageCount }} 页</span>
  </div>
</template>

<script type="text/babel">
  export default {
    name: 'ElPagination',

    props: {
      currentPage: Number,

      pageCount: Number
    },

    methods: {
      onPagerClick(event) {
        event.preventDefault();
        event.stopPropagation();
        const target = event.target;
        if (target.tagName === 'UL') {
          return;
        }
        if (target.tagName === 'SPAN' && target.parentNode.tagName === 'LI') {
          return;
        }

        let newPage;
        let currentText = target.textContent;
        const pageCount = this.pageCount;
        const currentPage = this.currentPage;

        if (currentText === '首页') {
            newPage = 1;
        }else if (currentText === '尾页') {
            newPage = pageCount;
        }else{
            newPage = Number(currentText);
        }

        /* istanbul ignore if */
        if (!isNaN(newPage)) {
          if (newPage < 1) {
            newPage = 1;
          }

          if (newPage > pageCount) {
            newPage = pageCount;
          }
        }

        if (newPage !== currentPage) {
          this.$emit('current-change', newPage);
        }
      }
    },

    computed: {
      pagers() {
        const pagerCount = 7;

        const currentPage = Number(this.currentPage);
        const pageCount = Number(this.pageCount);

        let showPrevMore = false;
        let showNextMore = false;

        if (pageCount > pagerCount) {
          if (currentPage > pagerCount - 3) {
            showPrevMore = true;
          }

          if (currentPage < pageCount - 3) {
            showNextMore = true;
          }
        }

        const array = [];

        if (showPrevMore && !showNextMore) {
          const startPage = pageCount - (pagerCount - 2);
          for (let i = startPage; i <= pageCount; i++) {
            array.push(i);
          }
        } else if (!showPrevMore && showNextMore) {
          for (let i = 3; i < pagerCount; i++) {
            array.push(i);
          }
        } else if (showPrevMore && showNextMore) {
          const offset = Math.floor(pagerCount / 2) - 1;
          for (let i = currentPage - offset ; i <= currentPage + offset; i++) {
            array.push(i);
          }
        } else {
          for (let i = 3; i <= pageCount; i++) {
            array.push(i);
          }
        }

        this.showPrevMore = showPrevMore;
        this.showNextMore = showNextMore;

        return array;
      }
    },

    data() {
      return {
        current: null,
        showPrevMore: false,
        showNextMore: false
      };
    }
  };
</script>



