<template>
    <div class="container">
        <div class="pages" v-loading="loading">
            <div class="mainwrap">
                <ul class="child-nav">
                    <li><router-link to="/works/online"><span class="eng">Online</span><br /><strong class="zh-cn">电商设计</strong></router-link></li>
                    <li><router-link to="/works/packing"><span class="eng">Packing</span><br /><strong class="zh-cn">包装设计</strong></router-link></li>
                    <li><router-link to="/works/brand"><span class="eng">Brand</span><br /><strong class="zh-cn">品牌设计</strong></router-link></li>
                    <li><router-link to="/works/shoot"><span class="eng">Shoot</span><br /><strong class="zh-cn">摄影/视频</strong></router-link></li>
                </ul>
                <div class="panel-hd">
                    <div class="emphasis-name"><strong>WORKS</strong><em>|</em><span>Online</span></div>
                    <div class="vice-txt">电商设计</div>
                </div>
                <div class="row word-list">
                    <div class="col-lg-4" v-for="(item, index) in currentPageData">
                        <div class="thumb-item">
                            <img :src="item.th" />
                            <div class="cover">
                                <div class="corner-lt">{{item.tp.toUpperCase()}}<br />DESIGN</div>
                                <div class="cover-nk"><strong>{{item.pa}}</strong>{{item.cl}}</div>
                                <div class="corner-rb"></div>
                            </div>
                            <a href="javascript:;" @click="handlePopup(item.id)"></a>
                        </div>
                    </div>
                </div>
                <nav class="srp-pager" aria-label="Page navigation">
                    <el-pagination @current-change="handleCurrentChange" :current-page="page" :page-count="total"></el-pagination>
                </nav>
            </div>
            <div class="sidebar">
                <cnode-sidebar></cnode-sidebar>
            </div>
        </div>
        <el-dialog :visible.sync="dialogVisible" width="100%" fullscreen>
            <div class="container">
                <div class="dialog-row row">
                    <div class="ascription">
                        <div class="brand-img" v-if="ctc.bd"></div>
                        <div class="title-name">{{ctc.pa}}</div>
                        <div class="part-line"></div>
                        <div class="title-name">所属行业</div>
                        <p class="sim-word">{{ctc.ca}}</p>
                        <div class="title-name mt15">服务内容</div>
                        <p class="sim-word">{{ctc.cl}}</p>
                        <div class="part-line"></div>
                        <p class="sim-word">{{ctc.pr}}</p>
                        <div class="part-line"></div>
                        <div class="title-name">相关案例</div>
                        <div class="smilier-case row">
                            <div class="col-xs-6">
                                <a class="tag-img"><img src="https://img.alicdn.com/bao/uploaded/i4/356060330/TB1FO9uXq4441JjSZFsXXcB8pXa_!!0-item_pic.jpg_240x240.jpg" /></a>
                            </div>
                            <div class="col-xs-6">
                                <a class="tag-img"><img src="https://img.alicdn.com/bao/uploaded/i4/356060330/TB1FO9uXq4441JjSZFsXXcB8pXa_!!0-item_pic.jpg_240x240.jpg" /></a>
                            </div>
                            <div class="col-xs-6">
                                <a class="tag-img"><img src="https://img.alicdn.com/bao/uploaded/i4/356060330/TB1FO9uXq4441JjSZFsXXcB8pXa_!!0-item_pic.jpg_240x240.jpg" /></a>
                            </div>
                            <div class="col-xs-6">
                                <a class="tag-img"><img src="https://img.alicdn.com/bao/uploaded/i4/356060330/TB1FO9uXq4441JjSZFsXXcB8pXa_!!0-item_pic.jpg_240x240.jpg" /></a>
                            </div>
                        </div>
                    </div>
                    <div class="work-show" v-html="ctc.de"></div>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import cnodeSidebar from '@/components/Sidebar'
import elPagination from '@/components/pagination'
import elDialog from '@/components/Dialog'

function filterTableData(ary, key, val){
    return ary.filter(function(element){
        return element[key] === val;
    });
}

export default {
    name: 'Works',
    components: {
        cnodeSidebar,
        elPagination,
        elDialog
    },
    data() {
        return {
            loading: true,
            total: 0,
            page: 1,
            tableData: [],
            ctc: {
                bd: '',
                pa: '',
                ca: '',
                cl: '',
                th: '',
                pr: '',
                de: '',
                lk: '',
                me: []
            },
            dialogVisible: false
        };
    },
    computed: {
        currentPageData() {
            var pc = 9;
            return this.tableData.slice((this.page - 1) * pc, this.page * pc);
        }
    },
    methods: {
        handleCurrentChange(val) {
            this.loading = true;
            this.page = val;
            this.handleCurrentData();
        },
        handleCurrentData(){
            setTimeout(()=>{
                this.loading = false;
            }, 300 + Math.round(Math.random()*200));
        },
        handlePopup (pt) {
            var ct = filterTableData(this.currentPageData, 'id', pt)[0];
            this.ctc.pa = ct.pa;
            this.ctc.ca = ct.ca;
            this.ctc.cl = ct.cl;
            this.ctc.th = ct.th;
            this.ctc.pr = ct.pr;
            this.ctc.de = ct.de;
            this.ctc.lk = ct.lk;
            this.dialogVisible = true;
        },
        getData() {
            this.$http({
                url: '/static/mongodb.json',
                method: 'get',
            }).then((res) => {
                this.tableData = filterTableData(res.data.caser, 'tp', this.$route.params.type);
                this.inc = parseInt(res.data.inc);
                this.total = Math.ceil(this.tableData.length/9);
                this.loading = false;
            }).catch((res) => {
                console.log('Works.vue: ', res);
            });
        }
    },
    mounted() {
        this.getData();
    }
}
</script>



